<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">量化分账户系统</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click="login('ruleForm')">登录</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default {
  name: 'login',
  data () {
    return {
      ruleForm: {
        account: localStorage.account ? localStorage.account : '',
        checkPass: localStorage.checkPass ? localStorage.checkPass : ''
      },
      rules: {
        account: [{
          required: true,
          message: '请输入账号',
          trigger: 'blur'
        }],
        checkPass: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        }]
      },
      checked: true
    }
  },
  mounted: function () {
    if (localStorage.account) {
      this.checked = true
    } else {
      this.checked = false
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.ruleForm.account !== 'admin' || this.ruleForm.checkPass !== '666666'&& this.ruleForm.checkPass !== '777777') {
            this.$message({
              message: '用户名/密码错误',
              type: 'error'
            })
          } else {
            if (this.checked) {
              localStorage.account = this.ruleForm.account
              localStorage.checkPass = this.ruleForm.checkPass
            } else {
              localStorage.removeItem('account')
              localStorage.removeItem('checkPass')
              // this.ruleForm.account = ''
              // this.ruleForm.checkPass = ''
            }
            if(this.ruleForm.checkPass == '777777'){
              this.$router.push({ path: '/SubAccountManagement' })
            }else{
              this.$router.push({ path: '/TotalAccountManagement' })
            }
            
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
    ,
      login() {
          // 获取已有账号密码
          this.$http.get('/api/login/getAccount')
              .then((response) => {
                  // 响应成功回调
                  console.log(response)
                  let params = {
                      account : this.ruleForm.account,
                      password : this.ruleForm.checkPass
                  };
                  // 创建一个账号密码
                  return this.$http.post('/api/login/createAccount',params);
              })
              .then((response) => {
                  console.log(response)
              })
              .catch((reject) => {
                  console.log(reject)
              });
      }
  },
  watch: {},
  components: {}
}
</script>

<style scoped>
.login-container {
    border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  background-color:#ffffff;
  /*background-color:#13CE66;*/
}

.title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
    .el-form-item{
      padding-bottom: 10px;
    }
</style>




